function mainSlider() {
    var swiper = new Swiper('.main .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 10,
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        pagination: {
            el: '.main .swiper-pagination',
            type: 'progressbar',
            clickable: true,
        },
        on: {
            init: function () {
                slidePaginationSet(this.activeIndex+1)
            },
            slideChange: function () {
                slidePaginationSet(this.activeIndex+1)
            },
        },
    })

    function slidePaginationSet(current) {
        let slidesCount = $(".main .swiper-slide").length
        $(".main__slider-current").html(current)
        $(".main__slider-all").html(slidesCount)
    }
}


let trustSlidersArray = []

function trustSliders() {
    $(".trust__slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        trustSlidersArray[index] = new Swiper(`.trust__slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: 5,
            spaceBetween: 31,
            navigation: {
                nextEl: `.trust__slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.trust__slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
        })
    })
}


mainSlider()
trustSliders()
